
.resume {
  margin: 0 auto;
}

.resume {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 800px;
  border: 1px solid #E8E8E8;
  padding-right: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.basic {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 35%;
  padding: 20px 30px;
  background-color: #E8E8E8;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.basic--persional {
  margin-top: 25px;
  /* padding: 20px 20px; */
}
.basic--persional .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.basic--persional .avatar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100px;
  height: 100px;
  border: 1px black solid;
  border-radius: 50%;
  margin: 0 auto;
}
.basic--persional .avatar img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
}
.basic--persional .name {
  margin: 10px 0;
}
.basic--persional .position {
  margin-bottom: 30px;
}
.basic--intent .content span {
  padding-left: 20px;
}
.basic--information .content {
  padding-left: 20px;
  white-space: normal;
}
.basic--information .content > div {
  margin: 5px 0;
}
.basic--critical .content {
  padding-left: 20px;
}
.ext {
  width: 65%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 30px 0px 50px 30px;
}
.block {
  width: 100%;
}
.block--header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 20px 0;
}
.block--header img {
  width: 40px;
  height: 40px;
  margin-right: 20px;
}
.block--header span {
  font-size: 20px;
  font-family: PingFangSC-Semibold;
}
.block--body {
  width: 100%;
}
.block--item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.block--item.minor {
  padding-left: 10px;
}
.block--item-left,
.block--item-right {
  /* width: 50%; */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-right: 5px;
}
.block--item-left {
  width: 110px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.divider {
  width: 100%;
  height: 1px;
  background-color: #C3C3C3;
  margin: 20px 0;
}
/* 图文 */
.img-text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 5px;
}
.img-text--img {
  width: 30px;
  height: 30px;
  margin-right: 15px;
}
.img-text--img.small {
  width: 20px;
  height: 20px;
}
.img-text--text {
  font-family: PingFangSC;
  font-size: 14px;
}
.img-text--text.bold {
  font-family: PingFangSC-Semibold;
  font-size: 16px;
}
/* font */
.font-s,
.font-m,
.font-l,
.font-xl,
.font-xxl {
  font-family: PingFangSC;
}
.font-s-bold,
.font-m-bold,
.font-l-bold,
.font-xl-bold,
.font-xxl-bold {
  font-family: PingFangSC-Semibold;
}
.font-s,
.font-s-bold {
  font-size: 12px;
}
.font-m,
.font-m-bold {
  font-size: 14px;
}
.font-l,
.font-l-bold {
  font-size: 16px;
}
.font-xl,
.font-xl-bold {
  font-size: 18px;
}
.font-xxl,
.font-xxl-bold {
  font-size: 20px;
}
/* color */
.color-blue {
  color: #3F6BEE;
}
.color-normal {
  color: #1B1A18;
}
.color-grey {
  color: #727171;
}
